const CustomBox = ({ children }) => (
  <Box
    sx={{
      backgroundColor: "#ffffff",
      borderRadius: 2,
      p: { xs: 2, md: 3 },
      boxShadow: "0 6px 20px rgba(0,0,0,0.05)",
      mb: 3,

      // Apply styles to all TextFields inside this Box
      "& .MuiTextField-root": {
        "& .MuiInputLabel-root": {
          color: "var(--bs-primary2)", // label color
        },
        "& .MuiOutlinedInput-input": {
          color: "var(--bs-primary2) !important", // input value color
        },
        "& .MuiOutlinedInput-root": {
          "& fieldset": {
            borderColor: "var(--bs-primary2)", // default border color
          },
          "&:hover fieldset": {
            borderColor: "var(--bs-primary2)", // hover border color
          },
          "&.Mui-focused fieldset": {
            borderColor: "var(--bs-primary2)", // focus border color
          },
        },
      },
    }}
  >
    {children}
  </Box>
);

