const NewBankTransfer = () => {
  const [suggestions, setSuggestions] = useState([]);
  const [error, setError] = useState("");

  const {
    globalLoader,
    setGlobalLoader,
    mobile,
    setMobile,
    setCurrentPage,
    currentPage,
    setCustomer,
    selectedBank,
    setSelectedBank,
    allBank,
    setAllBank,
  } = useBankTransfer();
  console.log("--------->", currentPage);

  const fetchSuggestions = useCallback(async () => {
    try {
      const response = await sagestMobileNumber({ mobile });
      const numbers = response?.data?.data || [];
      setSuggestions(numbers);
    } catch (err) {
      console.error("Error fetching suggestions:", err);
      setSuggestions([]);
    }
  }, [mobile]);

  useEffect(() => {
    if (!mobile) {
      setSuggestions([]);
      return;
    }

    const timeout = setTimeout(fetchSuggestions, 500);
    return () => clearTimeout(timeout);
  }, [mobile, fetchSuggestions]);

  const handleInputChange = (_, newValue) => {
    // Clean non-digits and limit to 10 characters
    const val = newValue.replace(/\D/g, "").slice(0, 10);

    setMobile(val); // Update only trimmed value

    // Now validate the trimmed value
    if (!val) {
      setError("");
    } else if (val.length < 10 || !isValidIndianMobile(val)) {
      setError("Enter a valid 10-digit mobile number.");
    } else {
      setError("");
    }
  };

  const handleSearch = async (e) => {
    e.preventDefault();
    if (!isValidIndianMobile(mobile)) {
      setError("Please enter a valid mobile number.");
      return;
    }
    setGlobalLoader(true);
    let body = {
      mobile: mobile,
      api_id: selectedBank,
    };
    try {
      let res = await CustomerInfo(body);
      if (res?.data?.statusCode === "401") {
        setCurrentPage("add-customer");
      }
      if (res?.data?.statusCode === "008") setCurrentPage("otp-page");

      if (res?.data?.statusCode === "007") {
        setCurrentPage("marchant-onboarding-otp");
      }
      if (res?.data?.statusCode === "200") {
        setCustomer(res?.data?.data?.response);
        setCurrentPage("send-money-page");
      }

      toastMessage(res?.data?.error, res?.data?.message);
    } catch (error) {
    } finally {
      setGlobalLoader(false);
    }
  };

  const getBank = async () => {
    let res = await settingBank();
    setAllBank(res?.data?.data?.dmtApiType || []);
    setSelectedBank(res?.data?.data?.defaultApi);
  };

  useEffect(() => {
    getBank();
  }, []);

  const handleBankChange = (id) => {
    // navigate('/bank-transfer')
    setSelectedBank(id);
  };
  return (
    <Box
      sx={{
        backgroundColor: "#F5F7FA",
        minHeight: "100vh",
        p: 2,
      }}
    >
      {globalLoader && <Loader />}
      <div className="PageHeading">
        <h1>Bank Transfer</h1>
      </div>

      {currentPage === "money-transfer" && (
        <>
          <CustomBox>
            <Typography variant="h6" fontWeight="600" mb={2}>
              Bank Selection
            </Typography>
            <Grid container spacing={2}>
              {allBank?.map((bank) => {
                const isSelected = selectedBank === bank._id;

                return (
                  <Grid item xs={6} sm={3} md={3} lg={2} key={bank._id}>
                    <Card
                      elevation={isSelected ? 6 : 1}
                      sx={{
                        borderRadius: 3,
                        border: isSelected
                          ? "2px solid var(--bs-nav)"
                          : "1px solid #ddd",
                        boxShadow: isSelected
                          ? "0 6px 18px rgba(0,0,0,0.12)"
                          : "none",
                        transition: "all 0.25s ease",
                        cursor: "pointer",
                      }}
                    >
                      <CardActionArea
                        onClick={() => handleBankChange(bank._id)}
                      >
                        <CardContent
                          sx={{
                            p: 1,
                            color: "var(--bs-primary2)",
                          }}
                        >
                          <Typography
                            variant="subtitle1"
                            align="center"
                            sx={{
                              fontSize: 14,
                              fontWeight: 600,
                              letterSpacing: "0.5px",
                            }}
                          >
                            {bank.name.toUpperCase()}
                          </Typography>
                        </CardContent>
                        {isSelected && (
                          <CheckCircle
                            sx={{
                              position: "absolute",
                              top: 11,
                              right: 30,
                              width: "17px",
                              height: "17px",
                              color: "var(--bs-primary2)",
                              fill: "#28a745",
                            }}
                          />
                        )}
                      </CardActionArea>
                    </Card>
                  </Grid>
                );
              })}
            </Grid>
            <Box
              sx={{
                // width:'50%',
                mt: 3,
                p: 2,
                borderRadius: 2,
                backgroundColor: "#fff",
                boxShadow: "0 4px 12px rgba(0,0,0,0.05)",
                //   maxWidth: 800,
              }}
            >
              <Typography variant="h6" fontWeight="600" mb={2}>
                Customer Search
              </Typography>

              {/* Main Row */}
              <form onSubmit={handleSearch}>
                <Box
                  display="flex"
                  flexDirection={{ xs: "column", md: "row" }}
                  alignItems="stretch"
                  gap={1.5}
                >
                  <Box display="flex" alignItems="center" gap={1} flex={1}>
                    {/* Icon Box */}
                    <Box
                      sx={{
                        width: 40,
                        height: 40,
                        //   border: '1px solid #d0d0d0',
                        borderRadius: 1,
                        display: "flex",
                        alignItems: "center",
                        justifyContent: "center",
                        color: "var(--bs-primary2)",
                      }}
                    >
                      <SearchIcon />
                    </Box>

                    {/* Input Field */}

                    <Autocomplete
                      freeSolo
                      options={suggestions}
                      getOptionLabel={(option) =>
                        typeof option === "string"
                          ? option
                          : option.customer_number
                      }
                      inputValue={mobile}
                      onInputChange={handleInputChange}
                      fullWidth
                      renderInput={(params) => (
                        <CustomTextField
                          {...params}
                          label="Search customer mobile number"
                          variant="outlined"
                          size="small"
                          error={!!error}
                          helperText={error ? error : null}
                        />
                      )}
                    />
                  </Box>

                  {/* Button */}
                  <Button
                    variant="contained"
                    disabled={!isValidIndianMobile(mobile)}
                    type="submit"
                    sx={{
                      width: { xs: "100%", md: "200px" },
                      mt: { xs: 1, md: 0 },
                      backgroundColor: "var(--bs-primary1)",
                      border: "1px solid var(--bs-primary2)",
                      color: "var(--bs-primary2) !important",
                      "&:hover": {
                        backgroundColor: "var(--bs-primary1)",
                        color: "var(--bs-primary2) !important",
                        opacity: 0.9,
                      },
                      "&.Mui-disabled": {
                        backgroundColor: "var(--bs-primary1) !important",
                        color: "var(--bs-primary2) !important",
                        borderColor: "var(--bs-primary2) !important",
                        opacity: 0.5,
                      },
                    }}
                  >
                    Search
                  </Button>
                </Box>
                {mobile?.length !== 10 && (
                  <Typography
                    variant="caption"
                    sx={{
                      display: "block",
                      textAlign: "right",
                      color: mobile.length === 10 ? "green" : "red",
                      mt: 0.5,
                    }}
                  >
                    Your current mobile number length is {mobile.length}
                  </Typography>
                )}
              </form>
            </Box>
          </CustomBox>
        </>
      )}

      {currentPage === "send-money-page" && <BankTransferMoneyTransferPage />}

      {currentPage === "add-newBeneficiaryPage" && <AddNewBeneficiaryPage />}

      {currentPage === "add-customer" && <AddCustomer />}

      {currentPage === "otp-page" && <OtpPopUp />}

      {currentPage === "marchant-onboarding-otp" && <MarchantOnboarding />}

      {currentPage === "KYC-page" && <KYCPage />}

      {currentPage === "invoice" && <InvoiceRecord />}
    </Box>
  );
};

export default NewBankTransfer;
